*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'montseratt', sans-serif;
}

.app{
  background-image: url('./images/cold-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;

}

.app.warm{
  background-image: url('./images/warm-bg.jpg');
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,.2), rgba(0,0,0,.75));
  padding: 25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  border: none;
  background: none;
  appearance: none;
  background-color: rgba(225,255,255,0.8) ;
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0,0,0,.2);
  color: #313131;
  font-size: 18px;
  transition: 0.4 ease;

}

.search-box .search-bar:focus{
  background-color: rgba(225,225,255,0.65);
  outline: none;
}

.location-box .location{
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,.5);
}

.location-box .date{
  color: #fff;
  font-size: 22px;
  font-weight: 350;
  text-align: center;
  margin: 5 px 2px;
  /* font-style: italic; */
  text-shadow: 2px 2px rgba(50,50,70,.5);
}

.whether-box{
  text-align: center;

}

.whether-box .temp{

  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(225,225,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 550;

  text-shadow: 3px 6px rgba(50,50,70,.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,.2);
}

.whether-box .whether{
  color: #fff;
  font-size: 48px;
  font-weight: 600;
  text-shadow: 2px 3px rgba(50,50,70,.2);
}

